<template>
  <div id="app">
    <mt-header fixed title="nCov-2019 map"></mt-header>
    <div id="mapdiv" ref="mapContainer"></div>
    <mt-cell title="标题文字"></mt-cell>
    <mt-cell title="标题文字" value="说明文字"></mt-cell>
  </div>
</template>

<script>
import mapboxgl from "mapbox-gl";
export default {
  name: "App",
  data() {
    return {};
  },
  methods: {
    init() {
      //创建地图对象
      mapboxgl.accessToken =
        "pk.eyJ1Ijoiamltd2VpIiwiYSI6ImNqN3ZoczZhZjZiZm0zMm8yeW00b2VkdjkifQ.Q_wogxtnFE11PatzGRkHEA";
      var map = new mapboxgl.Map({
        container: this.$refs.mapContainer,
        style: "mapbox://styles/jimwei/ck4mcmd4l7ufr1cpmh965ht2a"
      });
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style>
body {
  margin: 0;
}
#app {
  padding-top: 40px;
}
#mapdiv {
  width: 100%;
  height: 300px;
  /* background-color: aqua; */
}
</style>



